import callUs from '../../components/callUs';
import loginSheet from "../../components/loginSheet";
import { get,post } from "../../libs/request.js"

export default {
	data(){
		return {
			showSheet:false,
			showCallUs:false,
			moreInfo:{}
		}
	},
	onLoad(){
		this.checkAuth();
	},
	components:{
		loginSheet,
		callUs
	},
	methods:{
		async checkAuth() {
		  let that = this;
		  this.authCode = uni.getStorageSync("authCode") || null;
		  if (!this.authCode) {
		    that.showSheet = true;
		  } else {
			  this.getUserInfo()
		  }
		},
		authSuccess(e) {
		  this.authCode = uni.getStorageSync("authCode") || null;
		  this.showSheet = false;
		  this.getUserInfo();
		},
		goPage(path){
			uni.navigateTo({
			  url: path
			});
		},
		showCall(){
			this.showCallUs = true;
		},
		getUserInfo(){
			get('/login/info').then(res => {
				if(res.code === 200){
					this.moreInfo = res.data
				}
			});
		}
	}
};
